.outerHeaderContainer {
  background-color: #0f0f0f;
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: 1fr 928px 1fr;
  align-items: center;
  border-bottom: 1px solid #272727; }

.innerHeaderContainer {
  grid-column: 2 / -2;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header {
  font-family: "HelveticaNeueW01-55Roma", "HelveticaNeueW02-55Roma", "HelveticaNeueW10-55Roma", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 24px;
  color: #ffffff;
  text-decoration-line: none;
  display: flex;
  align-items: center; }

.logo {
  width: 57px;
  height: 43px;
  margin-right: 10px; }



