.container {
  width: 280px;
  border-radius: 4px;
  border: solid 1px #eeeeee;
  display: flex;
  flex-direction: column;
  padding: 36px 20px;
  align-items: center;
  margin-top: 10px; }

.avatar {
  width: 72px;
  height: 72px;
  border-radius: 50%; }

.devName {
  font-family: "HelveticaNeueW01-55Roma", "HelveticaNeueW02-55Roma", "HelveticaNeueW10-55Roma", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 20px;
  color: #333333;
  line-height: 1.5;
  margin-top: 21px; }

.socialIcon {
  font-size: 24px;
  color: #c7c7c7; }

.socialLinksContainer {
  display: flex;
  width: 70px;
  justify-content: space-between;
  margin-top: 17px; }

.socialLink {
  text-decoration-line: none; }

.bioText {
  font-family: "HelveticaNeueW01-45Ligh", "HelveticaNeueW02-45Ligh", "HelveticaNeueW10-45Ligh", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 24px; }

.reportIssue {
  font-family: "HelveticaNeueW01-45Ligh", "HelveticaNeueW02-45Ligh", "HelveticaNeueW10-45Ligh", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 14px;
  color: #e06154;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 11px;
  cursor: pointer; }

.reportFlag {
  width: 14px;
  height: 14px;
  margin-right: 7px; }



