.gameContainer {
  background-color: #0f0f0f;
  grid-column: 1 / -1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px 200px;
  height: calc(100vh - 284px); }

.gameWrapper {
  position: relative;
  width: 768px;
  height: 432px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }
  .gameWrapper.inFullScreen {
    position: absolute;
    top: 0;
    width: 100vw;
    height: calc(100vh - 48px - 20px); }

.gameFrame {
  border: none;
  width: 100%;
  height: 100%;
  display: none; }
  .gameFrame.gameFrameLoaded {
    display: block; }

.gameControls {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
  width: 768px;
  height: 432px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.buttonsContainer {
  width: 200px; }

.game {
  width: 768px;
  height: 432px; }

.spinnerContainer {
  height: calc(100vh - 64px - 104px);
  width: 100%;
  grid-column: 1 / -1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #ffffff; }

.loadingText {
  font-family: "HelveticaNeueW01-55Roma", "HelveticaNeueW02-55Roma", "HelveticaNeueW10-55Roma", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 24px; }

.aboutGameButton {
  background-color: transparent;
  border: 2px solid #e06154;
  color: #e06154;
  margin-top: 25px; }

.triangle {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 5px 0 5px;
  border-color: #e06154 transparent transparent transparent;
  margin-left: 5px; }

.fullScreenText {
  font-family: "HelveticaNeueW01-45Ligh", "HelveticaNeueW02-45Ligh", "HelveticaNeueW10-45Ligh", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 12px;
  color: #ffffff;
  opacity: 0.5;
  display: flex;
  align-items: center;
  align-self: flex-end;
  margin-top: 5px; }

.fullscreenIcon {
  fill: #ffffff;
  width: 12px;
  height: 12px;
  margin-left: 5px; }



