  height: 356px;
  border-radius: 12px;
  padding: 61px 48px 42px 48px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative; }

.cancel {
  position: absolute;
  right: 24px;
  top: 24px;
