.outerShowcaseContainer {
  display: grid;
  grid-template-columns: 1fr 928px 1fr;
  grid-column: 1 / -1;
  background-color: #ffffff;
  border-top: 5px solid #e06154; }

.innerShowcaseContainer {
  grid-column: 2 / -2;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  justify-content: center;
  height: calc(100vh - 30vh - 104px - 64px); }

.title {
  font-family: "HelveticaNeueW01-55Roma", "HelveticaNeueW02-55Roma", "HelveticaNeueW10-55Roma", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 24px;
  font-weight: 600;
  line-height: 1.3;
  color: #0f0f0f; }

.showcasesContainer {
  margin-top: 29px;
  display: flex;
  justify-content: center; }



