.overlay {
  top: 64px;
  height: calc(100vh - 64px);
  width: 100%;
  z-index: 100;
  background-color: #333333;
  opacity: 0.95;
  position: absolute;
  display: flex;
  justify-content: center; }

.resultsContainer {
  display: flex;
  flex-direction: column;
  width: 928px;
  margin-top: 39px; }

.noResults {
  color: #ffffff;
  font-family: "HelveticaNeueW01-45Ligh", "HelveticaNeueW02-45Ligh", "HelveticaNeueW10-45Ligh", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 24px; }

.backToGame {
  color: #e06154;
  font-family: "HelveticaNeueW01-45Ligh", "HelveticaNeueW02-45Ligh", "HelveticaNeueW10-45Ligh", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 14px;
  margin-top: 15px;
  cursor: pointer; }

.resultsCount {
  font-family: "HelveticaNeueW01-45Ligh", "HelveticaNeueW02-45Ligh", "HelveticaNeueW10-45Ligh", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 20px;
  color: #ffffff; }

.gameShowcases {
  margin-top: 17px;
  display: grid;
  grid-template-columns: repeat(auto-fit, 288px);
  grid-template-rows: repeat(2, 219px);
  grid-column-gap: 32px;
  grid-row-gap: 32px; }



