.outerGameHeader {
  display: grid;
  grid-template-columns: 1fr 928px 1fr;
  background-color: #272727;
  grid-column: 1 / -1;
  overflow: hidden;
  height: 120px;
  transition: height 400ms ease-in-out;
  position: absolute;
  width: 100%;
  top: calc(100vh - 120px); }
  .outerGameHeader.collapsed {
    height: 64px;
    position: fixed;
    top: 0; }
  .outerGameHeader.inFullScreen {
    height: 48px;
    top: calc(100vh - 48px); }

.placeHolder {
  height: 120px;
  transition: height 400ms ease-in-out; }
  .placeHolder.collapsed {
    height: 66px; }

.leftSide {
  position: relative;
  width: 100%;
  height: 100%; }

.devContainer {
  position: absolute;
  top: 50px;
  left: 0;
  transition: all 400ms ease-in-out; }
  .devContainer.collapsed {
    top: 24px;
    left: 270px; }

.ratingsContainer {
  position: absolute;
  top: 70px;
  left: 0;
  opacity: 1;
  transition: opacity 400ms ease-in-out; }
  .ratingsContainer.collapsed {
    opacity: 0; }

.innerGameHeader {
  grid-column: 2 / -2;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.gameName {
  font-family: "HelveticaNeueW01-55Roma", "HelveticaNeueW02-55Roma", "HelveticaNeueW10-55Roma", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 24px;
  color: #ffffff;
  display: inline-block;
  margin-top: 21px;
  transition: margin 400ms ease-in-out; }
  .gameName.collapsed {
    margin-top: 16px; }
  .gameName.inFullScreen {
    font-family: "HelveticaNeueW01-55Roma", "HelveticaNeueW02-55Roma", "HelveticaNeueW10-55Roma", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
    font-size: 18px;
    margin-top: 13px; }

.text {
  font-family: "HelveticaNeueW01-45Ligh", "HelveticaNeueW02-45Ligh", "HelveticaNeueW10-45Ligh", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 14px;
  color: #ffffff;
  line-height: 1.29;
  display: flex;
  align-items: center; }

.devLink {
  margin: 0 5px;
  color: #e06154;
  cursor: pointer; }

.rightSide {
  width: 287px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  position: relative; }

.donateButton, .backButton {
  transition: opacity 400ms ease-in-out;
  z-index: 2; }

.backButton {
  position: absolute;
  right: 0;
  cursor: pointer; }

.donateButton.hidden,
.backButton.hidden {
  opacity: 0;
  z-index: 0; }

.iconsContainer {
  display: flex;
  opacity: 1;
  position: absolute;
  left: 0;
  transition: opacity 400ms ease-in-out;
  z-index: 1; }
  .iconsContainer.collapsed {
    opacity: 0; }

.megaphoneIcon {
  width: 22px;
  transform: rotate(19deg); }

.shareIcon {
  width: 18px; }

.fullScreenText {
  font-family: "HelveticaNeueW01-45Ligh", "HelveticaNeueW02-45Ligh", "HelveticaNeueW10-45Ligh", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 12px;
  color: #ffffff;
  opacity: 0.5;
  display: flex;
  align-items: center;
  align-self: center;
  width: 100px; }

.fullscreenIcon {
  fill: #ffffff;
  width: 12px;
  height: 12px;
  margin-left: 5px; }



