.outerGameDetails {
  display: grid;
  grid-template-columns: 1fr 928px 1fr;
  grid-column: 1 / -1;
  background-color: #ffffff; }

.innerGameDetails {
  grid-column: 2 / -2;
  padding-top: 30px;
  display: flex; }

.text {
  font-family: "HelveticaNeueW01-45Ligh", "HelveticaNeueW02-45Ligh", "HelveticaNeueW10-45Ligh", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 16px;
  margin: 21px 0 16px 0;
  line-height: 24px;
  color: #333333; }

.leftSide {
  width: 608px;
  margin-right: 32px; }



