.container {
  position: relative;
  width: 208px;
  height: 24px; }

.searchIcon {
  right: 0;
  cursor: pointer;
  z-index: 0;
  position: absolute;
  opacity: 0;
  transition: opacity 300ms ease-in-out; }
  .searchIcon.visible {
    opacity: 1;
    z-index: 10; }

.field {
  font-family: "HelveticaNeueW01-45Ligh", "HelveticaNeueW02-45Ligh", "HelveticaNeueW10-45Ligh", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 16px;
  color: #363636;
  line-height: 1.5;
  position: absolute;
  opacity: 0;
  height: 24px;
  border: none;
  padding: 6px 18px;
  border-radius: 26.5px;
  outline: none;
  width: 1px;
  right: 0;
  top: -5px; }
  .field.visible {
    transition: opacity 300ms ease-in-out 300ms, width 300ms ease-in-out 300ms;
    opacity: 1;
    width: 172px; }
  .field.collapsing {
    transition: opacity 300ms ease-in-out, width 300ms ease-in-out;
    width: 1px;
    opacity: 0; }

.cancelButton {
  position: absolute;
  opacity: 0;
  right: 16px;
  top: 6px;
  width: 12px;
  height: 12px; }
  .cancelButton.visible {
    transition: opacity 300ms ease-in-out 300ms;
    opacity: 1;
    cursor: pointer; }



